.footer {
  list-style: none;
  background-color: var(--green);
  display: flex;
  flex-direction: column;
  background-color: var(--green);
  color: var(--blue);
  list-style: none;
  font-family: "Poppins";
}

.list {
  list-style-type: none;
  color: var(--blue);
}
.column {
  margin: 20px auto auto auto;
  align-items: center;
}

.store-title {
  margin-top: 56px;
}

.list-title {
  margin-top: 20px;
}

.link-footer {
  color: var(--blue);
  text-decoration: none;
}

.arrow {
  color: var(--blue);
  margin: 16px 16px 16px 361px;
}

.social-media {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.icons {
  margin-right: 11px;
  color: var(--blue);
}
